*{
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}
input{
  color: gray;
  font-size: 1.3rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border:1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.card p{
  padding: 5px;
  text-align: center;

}
.card img{
  height: 150px;
width: 300px;
}
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}